.content-page {
  flex-direction: column;
  justify-content: center;
}

.header-bg {
  background-color:white;
  border-radius: 5px;
  padding: 0 15px;
}

.border-right {
  border-right: 1px solid black !important;
}

.border-left {
  border-left: 1px solid black !important;
}

.padding-right {
  padding-right: 20px;
}

.padding-left {
  padding-left: 20px;
}

.text-right {
  text-align: right;
}

h2 {
  font-size: 12px;
  text-decoration: underline;
}

h3 {
  font-size: 12px;
  font-style: italic;
}

.flex-center-right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
}

.flex-end {
  display: flex;
  justify-content: flex-end;
}

.flex-start {
  display: flex;
  justify-content: flex-start;
}

.center {
  justify-content: center;
}

/* NAVBAR STYLES */
.navbar-light .navbar-nav .nav-link {
  color: rgba(0,0,0,.7);
}
.navbar-light .navbar-nav .nav-link:hover {
  color: #f44336;
  color: #ff5851;
}
.nav-link:hover {
  text-decoration: underline;
}
.navbar {
  background-color: white;
  padding: 5px 43px 5px 0;
}

.nav-header {
  margin-bottom: 0;
}

.navbar-links {
  padding-top: 0;
  padding-bottom: 3px;
}

.hide-nav {
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  opacity: 0;
  cursor: pointer;
}

.show-nav {
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  opacity: 1;
  cursor: pointer;
}

.nav-container {
  z-index: 100;
}

.current {
  text-decoration: underline;
  /* color: mediumvioletred; */
  color: #f44336;
  color: #ff5851;
  /* color: crimson; */
}

/* ABOUT ME STYLES */
.about-flex-collapse {
  display: flex;
}
@media (max-width: 960px) {
  .about-flex-collapse {
    flex-direction: column-reverse;
    align-items: center;
  }
}

.about-me-container {
  display: flex;
  align-content: center;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.about-me-paragraph {
  width: 300px;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-wrap: wrap;
  text-align: right;
}

.me {
  /* animation: App-logo-spin infinite 20s linear; */
  /* height: 40vmin; */
  /* pointer-events: none; */
  display: block;
  max-width: 60vmin;
  max-height: 40vmin;
  width: auto;
  height: auto;
}

/* RESUME STYLES */
.resume-container {
  display: flex;
  flex-direction: column;
}

.experience-container {
  display: flex;
  justify-content: flex-end;
}

.education-container {
  display: flex;
  justify-content: flex-start;
}

@media (max-width: 960px) {
  .navbar {
    background-color: transparent;
  }
  .navbar-collapse {
    background-color: white;
  }
  .nav-link {
    background-color: white;
  }
  .resume-break {
    display: none;
  }
  /* Experience */
  .experience-container {
    flex-direction: column-reverse;
    align-items: flex-end;
  }

  .experience-container .border-right {
    border-right: 0 !important;
  }

  .experience-container .padding-left {
    padding-left: 0;
  }

  .experience-container .padding-right {
    padding-right: 0;
  }

  .experience-container p {
    border-top: 1px solid black;
    padding-top: 10px;
  }

  /* Education */
  .education-container {
    flex-direction: column;
    align-items: flex-start;
  }

  .education-header {
    border-bottom: 1px solid black;
    /* padding-bottom: 5px; */
  }

  .education-container .border-left {
    border-left: 0 !important;
  }

  .education-container .padding-left {
    padding-right: 0;
  }

  .education-container .padding-right {
    padding-left: 0;
  }
}

.resume-text {
  text-align: right;
  max-width: 30vw;
}

.resume-divider {
  width: 1px;
  background-color: black;
  margin: 0 20px;
}

